.frontFooter {
  box-shadow: 0 0 10px #bbb;
  padding: 30px;
  font-size: 14px;
  position: absolute;
  width: 100%;
  bottom: 0; }
  .frontFooter a {
    color: #212529; }
  .frontFooter .container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-weight: bold;
    flex-wrap: wrap; }
    .frontFooter .container p {
      margin: 0; }
    .frontFooter .container .foot-menu span {
      margin: 0 15px; }
  @media screen and (max-width: 991px) {
    .frontFooter .container {
      text-align: center;
      overflow-y: none;
      justify-content: center !important; }
    .frontFooter .foot-menu {
      justify-content: center !important; } }
