.loginPage{
   padding: 75px;
   position: relative;
   min-height: 100vh;
   > .container{
    padding-bottom: 100px;
   }
   .input-group-append{
       max-width: 38%;
   }
   .small-text{
       font-size: 12px;
       font-weight: bold;
       color:rgb(122, 122, 122);
   }
   .logo{
    max-width: 350px;
   }
   .intro-text{
    font-size: 38px;
    font-weight: bold;
    color: #111;
    line-height: 46px;
    margin-top: 80px;
    margin-bottom: 30px;
    max-width: 500px;
   }
   .coins{
       max-width: 140px;
   }
   .loginForm{
       box-shadow: 0 0 10px #bbb;
       padding:30px;
       border-radius: 6px;
       h1{
           font-size: 28px;
           margin-bottom: 30px;
       }
   }
   .socialLogins{
       display: flex;
       align-items: center;
       justify-content: center;
       flex-wrap: wrap;
   } 
   .twitter-login-btn{
    width: 43px;
    height: 43px;
       width: auto;
       margin:10px;
   }
   .google-login-btn{
    width: 43px;
    height: 43px;
    background: url('../../assets/images/front/google-icon.svg') no-repeat center center;
    background-size: contain;
    border-radius: 6px;
    border:0;
    margin:10px;
   }
   .google-login-btn *{
       display: none;
   }
   .facebook-login-btn{
    width: 43px;
    margin:10px;
    height: 43px;
    background: url('../../assets/images/front/fb-icon.svg') no-repeat center center;
    border-radius: 6px;
    background-size: contain;   
    i{
        display: none;
    }
   }
   .new{
       margin-top: 60px;
       text-align: center;
       font-size: 16px;
       font-weight: bold;
       color: #7A91BA;
       margin-bottom: 2px;
   }
   .join{
       display: block;
       font-weight: bold;
       font-size: 14px;
       color: #5832E0;
       text-align: center;
   }
   .terms{
    font-size: 12px;
    .text-blue{
        color: #5832E0;
    }
}
   @media screen and (max-width: 991px) {
   .intro-text{
       font-size: 30px;
       margin-top: 50px;
   }
  }
  @media screen and (max-width: 767px) {
      padding:30px;
      .logo{
          width: 90%;
      }
    .intro-text{
        font-size: 140%;
        margin-top: 20px;
        line-height: 1.5;
        margin-bottom: 20px;
    }
    .coins{
        display: none;
    }
    .loginForm{
        padding:0;
        box-shadow: none;
    }
}
.react-dropdown-select{
    width: 100%;
    min-width: 130px;
}
}
.warnin{
    margin-top: 20px;
    margin-bottom: 0;
    font-size: 13px;
    color: #d9875f;
}